<template>
  <div class="line-collapse collapse">
    <div class="cursor flex center" @click="open" style="height: 50px; border-bottom: solid 2px #f2f2f2;">
      
    </div>
    <Collapse>
      <Panel ref="col3" name="3" v-if="$route.query.from!='product'">
        <span class="line-title-bar">
          <span style="width:180px; display:inline-block">
            360
          </span>
          <span class="btn" @click.stop="showAddInput('Add360Line')">
            <Icon type="plus" size="8"></Icon>
          </span>
        </span>
        <line-panel slot="content" :line="lines"></line-panel>
      </Panel>
    </Collapse>
    <Modal v-model="showAdd" width="600" @on-ok="submitAddLine">
      <p slot="header" style="color:#2d8cf0; text-align:center">
        <Icon type="compose"></Icon>
        <span></span>
      </p>
      <div style="padding-right: 20px;">
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
          <Form-item label="" prop="title">
            <Input v-model="formValidate.title" placeholder=""></Input>
          </Form-item>
          <!-- <FormItem label="">
              <RadioGroup v-model="formValidate.type">
                <Radio label="1"></Radio>
                <Radio label="2"></Radio>
              </RadioGroup>
            </FormItem> -->
        </Form>
      </div>
    </Modal>
  </div>
</template>

<script>
import LinePanel from './line-panel'
import LineInput from './line-input'
import UploadImg from './upload-img'
// import { hex_md5 } from '../../../util/md5'
const timestamp = Date.now()

export default {
  data: function() {
    return {
      lines: [],
      showAdd: false,
      formValidate: {
        parent_id: 0,
        title: '',
        type: 2,
        isSell: 1
      },
      ruleValidate: {
        title: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // uploader: {
      //   timestamp: timestamp,
      //   token: hex_md5('fitment_' + timestamp),
      //   type: 'images'
      // },
    }
  },
  components: {
    LinePanel,
    LineInput,
    UploadImg
  },
  methods: {
    open() {
      for (let i=0; i< this.lines.length; i++) {
        this.$refs['line'+i][0].toggle()
      }
    },
    showAddInput(id) {
      this.formValidate.parent_id = id
      this.formValidate.title = ''
      this.showAdd = !this.showAdd
    },
    async delProductCat(id) {
      this.$Modal.confirm({
        title: '',
        content: '<p></p>',
        loading: true,
        onOk: async () => {
          let { data } = await this.http('/productApi/productCatDel', {
            data: {
              ids: id
            },
            method: 'POST'
          })
          if (!data.code) {
            this.$Modal.remove();
            this.getList()
          }
          this.$Message.info(data.msg.zh);

        }
      });
    },
    async getList() {
      let { data } = await this.http('/productApi/getFullScenePicCat', {
        method: 'POST'
      })
      this.lines = data.result.data
    },
    submitAddLine() {
      this.$refs['formValidate'].validate(async (valid) => {
        if (valid) {
          let { data } = await this.http('/productApi/productCatNew', {
            data: {
              parent_id: this.formValidate.parent_id,
              title: this.formValidate.title,
            },
            method: 'POST'
          })
          if (!data.code) {
            this.$Message.success('!');
            this.getList()
          }
        } else {
          this.$Message.error('!');
        }
      })

    }
  },
  created() {
    this.getList()
    this.$bus.on('getProductLines', () => {
      this.getList()
    })
  }
}
</script>

<style lang="less">
.line-collapse {
  .ivu-collapse {
    background: none;
    border: none;
    .ivu-icon-arrow-right-b {
      font-size: 12px;
      font-weight: normal;
    }
    .ivu-icon-arrow-right-b:before {
      content: "\F218"
    }
    .ivu-collapse-header {
      padding-left: 16px;
      font-size: 14px;
      font-weight: bold;
      .btn {
        color: #ff0000;
        padding: 3px 2px;
      }
    }
    .ivu-collapse-content {
      background: none;
    }
    .ivu-collapse-content-box {
      padding-top: 0;
    }
    li {
      padding: 5px 0px 5px 12px;
    }
  }
}
</style>




